.footer {
    background-color: #33d1cc;
    h4 {
        margin-bottom: 20px;
    }
    .socialMedia {
        align-items: center;
        margin-bottom: 30px;
    
        li {
            display: inline-block;
            vertical-align: middle;
    
            &+ li {
                margin-left: 15px;
            }
    
            a {
                color: #fff;
                display: block;
                font-size: 20px;
                transition: all 0.2s ease-in-out;
    
                &:hover > svg {
                    fill: #ff00bb;
                    transition: all 0.2s ease-in-out;
                }
            }
        }
    
    }
    .contactUs {
        li {
            list-style: none;
            &+li {margin-top: 10px}
        }
        a {
            color: #000;
            transition: all 0.3s ease-in-out;
            &:hover {
                color: #ff00bb;
            }
        }
    }
    
    .copyRight {
        background-color: #19283f;
        padding: 15px 0;
        p{
            font-size: 12px;
            margin-bottom: 0;
            color: #ffffff;
        }
    
    }
}