*, ::after, ::before {
    box-sizing: border-box;
}
@font-face { font-family: "Avenir"; src: url('../font/Avenir.woff.ttf'); } 
body{background-color: rgba(255, 241, 236, 0.5); color: rgba(89,89,89,1); font-family: 'Avenir', sans-serif; font-size: 16px; line-height: normal; margin: 0; font-weight: 400; overflow-x: hidden}
/* default styles written by arun*/
img{max-width: 100%; border: 0; vertical-align: middle}
h1, h2, h3, h4, h5, h6{color: #1a2940; font-family: 'Avenir', sans-serif; font-size: 40px; font-weight: 700; margin: 0px; line-height: 1.2em}
h2{font-size: 30px}
h3{font-size: 20px}
h4{font-size: 18px}
h5{font-size: 15px}
h6{font-size: 14px}
p{line-height: 24px; margin: 0 0 15px; padding: 0}
ul{display: block; margin: 0; padding: 0}
ul li{line-height: 24px}
a {text-decoration: none; cursor: pointer; -moz-transition: all linear 0.2s; -ms-transition: all linear 0.2s; -o-transition: all linear 0.2s; -webkit-transition: all linear 0.2s; transition: all linear 0.2s; color: #a8b9c0}
a:hover, .a-link:hover, a:focus{outline: medium none; text-decoration: none; color: #33d1cc}
a img{border: 0 none}
a * {-mos-transition: all linear 0.2s; -ms-transition: all linear 0.2s; -o-transition: all linear 0.2s; -webkit-transition: all linear 0.2s; transition: all linear 0.2s}
.fontBold{font-weight: 700}
.textUpper{text-transform: uppercase}
.textLower{text-transform: lowercase}
.textCaseNone{text-transform: none}
.textRight{text-align: right}
.textLeft{text-align: left}
.textCenter{text-align: center}
.textEllipsis{width: 100%; display: block; overflow: hidden; text-overflow: ellipsis; white-space: nowrap}
/*------------btn default------*/
.btn, a.btn{font-weight: 400; font-size: 15px; text-align: center; color: #fff; display: inline-block; padding: 14px; position: relative; cursor: pointer; border-radius: 5px; border: none; transition: all linear 0.2s; min-width: 205px; background-color: #ff00bb; font-family: 'Avenir', sans-serif;}
.btn:hover, .btn:focus{ transform: translateY(-2px); box-shadow: 0px 0px 7px 0px rgba(51, 209, 204, 0.11); color: #fff !important;}
.btn.tiny{min-width: 110px; padding: 8px;}
.btn.btnPrimary {
  background-color: #ff00bb;
}
.btn.btnSecondary{
  background-color: #33d1cc;
}
.contentSect{padding: 60px 0}
.svgIcon {
    width: 28px;
    height: 28px;
    display: inline-block;
    vertical-align: middle;
}

.headSect {
  margin-bottom: 30px; 

  p {
    padding-top: 20px;
  }
}
