html{
  font-size: 100%;
}

#loginform{
  padding: 25px;
  max-width: 500px;
  min-width: 300px;
  max-height: 700px;
  width: 60%;
  margin: 0 auto;
  background-color: #FFFFFF;
  border-radius: 25px;
}

#headerTitle{
  text-align: center;
  padding: 2rem 0;
  margin: 0;
  font-size: 2rem;
}

.form-row {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
  max-width: 100%;
}

.login {
    padding-top: 13%;
}

.form-row input:not([type="checkbox"]) {
  width: 100%;
  box-sizing: border-box;
	border: none;
  font-size: .8rem;
  padding-left: 1.5rem;
  padding-bottom: 1rem;
  box-shadow: inset 0px -3px 0px 0px #ff00bb;
  transition: opacity 0.2s ease-in;
}

.form-row input:not([type="checkbox"]):focus{
   box-shadow: inset 0px -3px 0px 0px #ff00bb;
   outline: none;
}

.form-row input::-webkit-input-placeholder{
  opacity: 1;
  transition: opacity 0.25s ease-out;
}

.form-row input:hover::-webkit-input-placeholder,
.form-row input:focus::-webkit-input-placeholder{
  opacity: 0;
}

.form-row label{
  font-size: 1rem;
  align-self: start;
  padding-bottom: 1.5rem;
  color: rgba(0, 0, 0, 0.9);

  &.optionLabel {
    font-weight: 400;
    font-size: .8rem;
    margin-left: 5px;
    padding: 0px;
  }
}

.checkbox {
  margin-bottom: 15px;
}

.form-row button{
  border-radius: 25px;
  width: 50%;
  height: 40px;
  font-size: 1rem;
  color: white;
  font-weight: 700;
  background: rgb(34,193,195);
  background: linear-gradient(90deg, #ff00bb 0%,     #ff00bb 100%);
  border: 0px;
  cursor: pointer;
  transition: opacity 0.25s ease-out;
}

.form-row button:hover{
  opacity: 0.8;
}

#button{
  padding-top: 2.5rem;
  padding-bottom: 1.5rem;
}

#facebookIcon{
  width: 1.8rem;
  height: 1.8rem;
}

#twitterIcon{
  width: 1.8rem;
  height: 1.8rem;
}

#googleIcon{
  width: 1.8rem;
  height: 1.8rem;
}

#iconGroup{
  display: flex;
  justify-content: center;
  padding: 2rem 0;
}

#iconGroup a{
  margin: 0 0.6rem;
}

#alternativeLogin {
   text-align: center;
}

.registerLink {
  font-size: 18px;
  color: #ff00bb;
}


