.jobDetails {
  .jobImg, .jobDescription {
    margin-bottom: 40px;
  }
  .priceRow {
    margin-bottom: 40px;
  }
}
.price {
  display: flex;
  align-items: flex-end;

  .currency {
    font-size: 40px;
    font-weight: 700;
    line-height: 33px;
    color: #ff00bb;
    &::before {
      content: "\00A3";
    }
  }

  .priceLabel {
    margin-left: 5px;
  }
}

@media screen and (max-width: 768px) {
  .buttonContainer {
    .btn {
      min-width: 120px;
    }
  }
}