.card{
  padding: 0 15px 30px;
  width: 100%;
  display: flex;
  overflow: hidden;

  .cardWrapper{
    background-color: #fff;
    box-shadow: 0px 5px 15px 0px rgba(0,0,0,.08);
    border-radius: 16px;
    padding: 25px;
    display: flex;
    width: 100%;
    overflow: hidden;
    .cardImg {
      flex-basis: 120px;
      flex-shrink: 0;
      margin-right: 25px;

      img{    
        width: 120px;
        height: 120px;
        object-fit: cover;
        object-position: top;
      }
    }
    h4 {
      margin-bottom: 10px;
      font-weight: 400;
    }
    a {
      color: rgba(255,0,187, .5);
  
      &:hover {
        color: rgba(255,0,187, 1)
    
      } 
    }
    @media (max-width: 576px) {
      flex-direction: column;
      .cardImg {
        margin-right: 0px;
        flex-basis: auto;
        margin: -25px -25px 25px;
        img{    
          width: 100%;
          height: 200px;
        }
      }
    }

  }
}