.bannerSect {
  min-height: calc(100vh - 70px);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  padding: 30px 0;

  .bannerContents {
    align-items: flex-end;
    h1 {
      font-size: 40px;
      color: #fff;
    }
    h2{
      margin-top: 10px;
      font-weight: 400;
      color: #000000;
    }
    p{ 
      margin: 10px 0 0;
      color: #000000;
      width: 35%;
    }
    .btn {
      margin-top: 15px;
    }
  }
}