.topBar {
  background-color: #ff00bb;
  padding: 15px 0;
  transition: all linear 0.2s;
  box-shadow: 0 0 7px rgba(0,0,0,.1);
  position: relative;
}
.logoSect {
  max-width: 227px;
  height: auto;
}
.logoSect a {
  display: block;
}
.navBar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0; 
  list-style: none;
}
.navBar li + li {
  margin-left: 30px;
}
.navBar li a {
  display: block;
  color: #ffffff;
}

#closeMenu{text-align: right; padding: 14px 0 0; position: relative; z-index: 9999; display: block; width: 30px; margin-right: 0; margin-left: auto; color: #fff; cursor: pointer}

.overLay{background-color: rgba(0, 0, 0, 0); -moz-transition: all linear 0.2s; -ms-transition: all linear 0.2s; -o-transition: all linear 0.2s; -webkit-transition: all linear 0.2s; transition: all linear 0.2s}
.overLay.fixed{top: 0; left: 0; right: 0; bottom: 0; z-index: 30; position: fixed; opacity: 1; background-color: rgba(0, 0, 0, 0.8)}
/*********responsive styles*********/
@media only screen and (min-width: 992px){
	.show-sm {display: none !important}
}
@media only screen and (max-width: 991px){
  .siteNav {
    position: fixed;
    top: 0;
    right: -60%;
    width: 50%;
    z-index: 999;
    background-color: #33d1cc;
    overflow: auto;
    height: 100%;
    -ms-transition: all linear 0.3s;
    -moz-transition: all linear 0.3s;
    -webkit-transition: all linear 0.3s;
    -o-transition: all linear 0.3s;
    transition: all linear 0.3s;
    padding: 0;
    display: block !important;
    padding: 0 15px;
  }
  .siteNav.showNav {
      right: 0;
  }
	.siteNav .navBar{display: block; text-align: left}
	.siteNav .navBar li {margin: 0 !important}
	.siteNav .navBar li a {padding: 6px 0px; line-height: 32px; color: #fff !important; text-align: left;}
	.siteNav .nav li > a:hover {color: #fff; background-color: transparent}
	.siteNav .navBar li + li > a {border-top: 1px solid rgba(255, 255, 255, 0.1) !important}
	#closeMenu span {font-size: 40px; font-weight: 500; line-height: 24px; display: block; /* margin-right: -15px; */ color: #fff}
	/**********mobile menu custom ends here**********/
}

@media only screen and (max-width: 767px){
  .siteNav{
    width: 80%;
    right: -90%;
  }
}